@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);

.splash{height:100vh;width:100%;display:flex;justify-content:center;align-items:center}.splash .expand-icon{position:absolute;bottom:2rem;cursor:pointer;-webkit-animation:bobble 1s infinite alternate;animation:bobble 1s infinite alternate}@-webkit-keyframes bobble{0%{transform:translateY(0)}100%{transform:translateY(-1rem)}}@keyframes bobble{0%{transform:translateY(0)}100%{transform:translateY(-1rem)}}


body{margin:0;padding:0;font-family:'Poppins', sans-serif}

